@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&family=Inter:wght@300;400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    margin: 0;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@layer base {
    :root {
        /* Quiz Brand Colors - Warm, Romantic, Inviting */
        --background: 40 30% 97%;           /* Warm cream background */
        --foreground: 25 15% 15%;           /* Deep warm charcoal */
        
        --card: 40 25% 99%;                 /* Soft white cards */
        --card-foreground: 25 15% 15%;
        
        --popover: 40 25% 99%;
        --popover-foreground: 25 15% 15%;
        
        /* Primary - Elegant Emerald Green (Trust & Growth) */
        --primary: 152 55% 38%;             /* Rich emerald green */
        --primary-foreground: 0 0% 100%;
        --primary-hover: 152 55% 32%;
        --primary-light: 152 40% 92%;       /* Light mint for backgrounds */
        
        /* Secondary - Warm Rose (Love & Romance) */
        --secondary: 350 45% 95%;           /* Soft rose tint */
        --secondary-foreground: 350 55% 35%;
        
        /* Accent - Sunset Gold (Warmth & Passion) */
        --accent: 35 85% 90%;               /* Soft golden glow */
        --accent-foreground: 35 70% 30%;
        
        /* Muted tones for subtle elements */
        --muted: 35 15% 93%;
        --muted-foreground: 25 10% 45%;
        
        /* Destructive */
        --destructive: 0 72% 51%;
        --destructive-foreground: 0 0% 100%;
        
        /* Borders & Inputs */
        --border: 35 20% 88%;
        --input: 35 20% 88%;
        --ring: 152 55% 38%;
        
        /* Quiz specific tokens */
        --quiz-option-bg: 40 25% 99%;
        --quiz-option-hover: 152 30% 96%;
        --quiz-option-selected: 152 55% 38%;
        --quiz-option-selected-bg: 152 40% 96%;
        --quiz-progress: 152 55% 38%;
        
        /* Typography */
        --heading-color: 25 20% 18%;
        --body-color: 25 10% 35%;
        
        /* Shadows & Effects */
        --shadow-soft: 0 4px 20px -4px hsl(25 20% 20% / 0.08);
        --shadow-hover: 0 8px 30px -8px hsl(152 55% 38% / 0.2);
        --shadow-card: 0 2px 12px -2px hsl(25 20% 20% / 0.06);
        
        /* Transitions */
        --transition-smooth: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
        --transition-bounce: 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
        
        /* Radius */
        --radius: 0.75rem;
        --radius-lg: 1rem;
        --radius-xl: 1.5rem;
        --radius-full: 9999px;
        
        /* Chart colors */
        --chart-1: 152 55% 38%;
        --chart-2: 350 45% 55%;
        --chart-3: 35 70% 50%;
        --chart-4: 200 60% 50%;
        --chart-5: 280 55% 55%;
    }
    
    .dark {
        --background: 25 15% 8%;
        --foreground: 40 20% 95%;
        --card: 25 15% 12%;
        --card-foreground: 40 20% 95%;
        --popover: 25 15% 12%;
        --popover-foreground: 40 20% 95%;
        --primary: 152 50% 45%;
        --primary-foreground: 0 0% 100%;
        --secondary: 25 15% 18%;
        --secondary-foreground: 40 20% 85%;
        --muted: 25 15% 18%;
        --muted-foreground: 40 15% 60%;
        --accent: 35 70% 25%;
        --accent-foreground: 35 85% 90%;
        --destructive: 0 72% 45%;
        --destructive-foreground: 0 0% 100%;
        --border: 25 15% 20%;
        --input: 25 15% 20%;
        --ring: 152 50% 45%;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    body {
        @apply bg-background text-foreground;
    }
}

/* Typography Classes */
@layer components {
    .font-heading {
        font-family: 'Playfair Display', Georgia, serif;
    }
    
    .font-body {
        font-family: 'Inter', sans-serif;
    }
    
    /* Quiz Option Card Styles */
    .quiz-option {
        @apply relative bg-card border border-border rounded-xl p-4 cursor-pointer;
        @apply transition-all duration-300 ease-out;
        box-shadow: var(--shadow-soft);
    }
    
    .quiz-option:hover {
        @apply border-primary/30;
        box-shadow: var(--shadow-hover);
        transform: translateY(-2px);
    }
    
    .quiz-option.selected {
        @apply border-primary border-2;
        background: hsl(var(--quiz-option-selected-bg));
        box-shadow: var(--shadow-hover);
    }
    
    /* Gender Selection Cards */
    .gender-card {
        @apply relative overflow-hidden rounded-2xl cursor-pointer;
        @apply transition-all duration-300 ease-out;
        box-shadow: var(--shadow-card);
    }
    
    .gender-card:hover {
        box-shadow: var(--shadow-hover);
        transform: translateY(-4px);
    }
    
    /* Progress Bar */
    .quiz-progress {
        background: hsl(var(--primary-light));
    }
    
    .quiz-progress-bar {
        background: hsl(var(--primary));
        transition: width 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    /* Button Variants */
    .btn-quiz {
        @apply w-full py-4 px-8 rounded-full text-lg font-semibold;
        @apply transition-all duration-300;
        background: hsl(var(--primary));
        color: hsl(var(--primary-foreground));
        box-shadow: 0 4px 15px -3px hsl(var(--primary) / 0.4);
    }
    
    .btn-quiz:hover:not(:disabled) {
        background: hsl(var(--primary-hover));
        box-shadow: 0 6px 20px -3px hsl(var(--primary) / 0.5);
        transform: translateY(-2px);
    }
    
    .btn-quiz:disabled {
        @apply opacity-40 cursor-not-allowed;
        background: hsl(var(--muted-foreground));
        box-shadow: none;
    }
    
    /* Animation Keyframes */
    @keyframes fadeInUp {
        from {
            opacity: 0;
            transform: translateY(20px);
        }
        to {
            opacity: 1;
            transform: translateY(0);
        }
    }
    
    @keyframes fadeIn {
        from { opacity: 0; }
        to { opacity: 1; }
    }
    
    @keyframes slideInLeft {
        from {
            opacity: 0;
            transform: translateX(-30px);
        }
        to {
            opacity: 1;
            transform: translateX(0);
        }
    }
    
    @keyframes scaleIn {
        from {
            opacity: 0;
            transform: scale(0.9);
        }
        to {
            opacity: 1;
            transform: scale(1);
        }
    }
    
    @keyframes pulse-soft {
        0%, 100% { opacity: 1; }
        50% { opacity: 0.7; }
    }
    
    @keyframes float {
        0%, 100% { transform: translateY(0); }
        50% { transform: translateY(-10px); }
    }
    
    .animate-fade-in-up {
        animation: fadeInUp 0.6s ease-out forwards;
    }
    
    .animate-fade-in {
        animation: fadeIn 0.5s ease-out forwards;
    }
    
    .animate-slide-in {
        animation: slideInLeft 0.5s ease-out forwards;
    }
    
    .animate-scale-in {
        animation: scaleIn 0.4s ease-out forwards;
    }
    
    .animate-float {
        animation: float 3s ease-in-out infinite;
    }
    
    .animate-pulse-soft {
        animation: pulse-soft 2s ease-in-out infinite;
    }
    
    /* Stagger animations for lists */
    .stagger-1 { animation-delay: 0.1s; }
    .stagger-2 { animation-delay: 0.2s; }
    .stagger-3 { animation-delay: 0.3s; }
    .stagger-4 { animation-delay: 0.4s; }
    .stagger-5 { animation-delay: 0.5s; }
    .stagger-6 { animation-delay: 0.6s; }
}

/* Custom scrollbar */
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: hsl(var(--muted));
}

::-webkit-scrollbar-thumb {
    background: hsl(var(--border));
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--muted-foreground));
}

@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }

    [data-debug-wrapper="true"] > * {
        margin-left: inherit;
        margin-right: inherit;
        margin-top: inherit;
        margin-bottom: inherit;
        padding-left: inherit;
        padding-right: inherit;
        padding-top: inherit;
        padding-bottom: inherit;
        column-gap: inherit;
        row-gap: inherit;
        gap: inherit;
        border-left-width: inherit;
        border-right-width: inherit;
        border-top-width: inherit;
        border-bottom-width: inherit;
        border-left-style: inherit;
        border-right-style: inherit;
        border-top-style: inherit;
        border-bottom-style: inherit;
        border-left-color: inherit;
        border-right-color: inherit;
        border-top-color: inherit;
        border-bottom-color: inherit;
    }
}
